@import '../../variables.scss';

.room-selector-form {
  display: block;
  width: 100%;
  max-width: 380px;

  padding: 30px;

  background-color: white;

  border: 1px solid $fontColorLight25;
  box-shadow: 0 10px 20px $fontColorLight5;
  border-radius: 4px;

  &__title {
    display: block;

    margin-top: 9px;
    margin-bottom: 20px;

    color: $fontColor;

    font-family: $headerFont;
    font-size: 24px;
    line-height: 30px;
  }

  &__guests-dropdown {
    margin-top: 22px;
  }

  &__button {
    margin-top: 31px;
  }
}
