@import '../../variables.scss';

.dropdown {
  display: block;
  position: relative;
  width: 100%;

  background-color: white;

  line-height: 18px;

  &__label {
    margin-bottom: 4px;

    color: $fontColor;

    font-size: 12px;
    line-height: 15px;
    font-weight: bold;
    text-transform: uppercase;
  }

  &__header {
    display: block;
    position: relative;

    border: 1px solid $fontColorLight25;
    padding: 12px 90px 12px 14px;
    border-radius: 4px;

    color: $fontColorLight75;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    cursor: pointer;
  }

  &__header:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &_open &__header {
    border: 1px solid $fontColorLight50;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  &__header::before {
    content: '';
    position: absolute;
    width: 44px;
    height: 44px;
    right: 0;
    top: 0;
    bottom: 0;

    opacity: 0.75;

    background-image: url('img/arrow-down.svg');
    background-repeat: no-repeat;
    background-position: 17px 18px;
  }

  &__content {
    display: none;
    position: absolute;
    width: 100%;

    background-color: white;

    border: 1px solid $fontColorLight50;
    border-top: none;
    padding: 1px 7px 0 15px;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;

    z-index: 1;
  }

  &_open &__content {
    display: block;
  }

  &__content-item {
    margin-top: 3px;
    margin-bottom: 7px;
  }

  &__content-actions {
    display: flex;
    justify-content: space-between;

    margin-top: 21px;
    margin-bottom: 12px;
  }

  &__content-action-apply {
    padding: 0;

    color: $mainColor;
  }

  &__content-action-clean {
    padding: 0;

    color: $fontColorLight50;
  }

  &__content-action-apply:focus,
  &__content-action-clean:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }
}

[class^='dropdown__content-action-'] {
  background-color: white;
  border: none;
  outline: none;

  text-transform: uppercase;
  font-size: 12px;
  line-height: 15px;
  font-weight: bold;

  cursor: pointer;
}
