@import '../../../variables.scss';

.dropdown-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 30px;

  color: $fontColor;

  font-size: 12px;
  line-height: 15px;
  font-weight: bold;
  text-transform: uppercase;

  &__label {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 92px;

    margin-top: 3px;

    flex-shrink: 0;
  }

  &__action {
    display: bloc;
    width: 30px;
    height: 30px;

    border-radius: 15px;
    border: 1px solid $fontColorLight25;
    outline: none;

    background: white;
    color: $fontColorLight50;

    font-size: 18px;

    cursor: pointer;
  }

  &__action:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__value {
    display: block;
    flex-grow: 1;
    padding-bottom: 1px;

    text-align: center;
  }

  .dropdown-item__action_disabled {
    opacity: 0.38;

    cursor: auto;
  }
}
