@import '../../../variables.scss';

.footer-nav-block {
  width: 100%;
  max-width: 160px;

  @media (max-width: 992px) {
    display: none;
  }

  &__title {
    margin: 0;
    margin-bottom: 13px;

    color: $fontColorLight75;

    font-size: 12px;
    line-height: 24px;
    text-transform: uppercase;
  }

  &__item {
    margin-bottom: 14px;
  }

  &__link {
    color: $fontColorLight50;

    text-decoration: none;
  }

  &__link:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }
}
