@import '../../variables.scss';

.top-bar {
  position: relative;
  padding-top: 20px;
  padding-bottom: 8px;

  box-shadow: 0 10px 20px $fontColorLight5;

  @media (max-width: 970px) {
    padding-top: 20px;
  }

  @media (max-width: 600px) {
    height: auto;
  }

  &__logo-link:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__center {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: inherit;

    margin: 0 auto;
    padding: 0 20px;

    max-width: 1200px;

    @media (max-width: 970px) {
      flex-wrap: wrap;
    }

    @media (max-width: 600px) {
      display: block;
    }
  }

  &__menu {
    display: flex;
    flex-direction: row;
    margin-left: auto;

    margin-bottom: 6px;
    margin-right: 8px;

    transition: max-height, margin 0.1s ease-in-out;

    @media (max-width: 970px) {
      order: 3;
      width: 100%;
      margin-left: auto;
      justify-content: space-between;
      padding-top: 15px;
    }

    @media (max-width: 600px) {
      display: flex;
      flex-direction: column;
      align-items: start;
      height: auto;
      max-height: 500px;

      padding-top: 0;
      margin-top: 10px;
      margin-bottom: 10px;

      overflow: hidden;
    }
  }

  &__menu-item {
    display: block;

    padding-left: 20px;

    color: $fontColorLight50;

    font-size: 14px;

    cursor: pointer;

    @media (max-width: 600px) {
      width: auto;
    }
  }

  &__menu-expanded {
    position: relative;

    padding-right: 24px;
  }

  &__menu-expanded::before {
    display: block;
    position: absolute;
    content: '';
    width: 14px;
    height: 8px;
    right: 3px;
    top: 6px;

    background: url('img/expand-more-down.svg');
    background-repeat: no-repeat;
    opacity: 0.5;
  }

  &__submenu {
    display: none;
    position: absolute;
    width: 150px;
    left: 5px;

    padding-top: 10px;
    padding-left: 10px;
    box-shadow: 0 10px 20px $fontColorLight5;

    background-color: white;

    @media (max-width: 600px) {
      width: auto;
    }
  }

  &__menu-expanded:hover &__submenu {
    display: block;

    z-index: 100;

    @media (max-width: 600px) {
      display: block;
      position: relative;

      padding-top: 0;

      box-shadow: none;
    }
  }

  &__submenu-item {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &__menu-expanded:hover &__submenu-item {
    @media (max-width: 600px) {
      padding-top: 2px;
      padding-bottom: 2px;
    }
  }

  &__menu-link {
    color: $fontColorLight50;

    text-decoration: none;

    outline: none;
  }

  &__menu-link:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__menu-item_active {
    font-weight: bold;
  }

  &__user {
    margin-left: 15px;
    margin-bottom: 5px;

    @media (max-width: 970px) {
      order: 2;
    }

    @media (max-width: 600px) {
      margin-left: 0;
    }
  }

  &__switch-button {
    display: none;
    position: absolute;
    top: 27px;
    right: 30px;
    width: 24px;
    height: 24px;

    border: none;
    outline: none;

    background-color: transparent;
    background-image: url('img/close.svg');
    background-repeat: no-repeat;

    cursor: pointer;

    @media (max-width: 600px) {
      display: block;
    }
  }

  &__switch-button:focus {
    outline: auto;
    outline-color: $mainColor;
  }

  &_close &__menu {
    @media (max-width: 600px) {
      max-height: 0;
      margin-top: 0;
      margin-bottom: 0;
    }
  }

  &_close &__switch-button {
    @media (max-width: 600px) {
      background-image: url('img/menu.svg');
    }
  }
}
