@import 'normalize.css';
@import './fonts.scss';
@import './variables.scss';

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background-color: $bgColor;
  color: $fontColorLight75;

  font-family: $mainFont;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul {
  list-style-type: none;
}

ul,
li {
  padding: 0;
  margin: 0;
}

.invisible {
  visibility: hidden;
}

a {
  text-decoration: none;

  outline: none;
}

figure {
  margin: 0;
  padding: 0;
  border: none;
}

h1 {
  color: $fontColor;

  margin-top: 0;

  font-family: $headerFont;
  font-size: 24px;
  font-weight: bold;
  line-height: 30px;
}

h2 {
  color: $fontColor;

  margin-top: 0;

  font-family: $headerFont;
  font-size: 19px;
  font-weight: bold;
  line-height: 24px;
}

h3 {
  color: $fontColor;

  margin-top: 4px;

  font-family: $mainFont;
  font-size: 12px;
  font-weight: bold;
  line-height: 15px;
  text-transform: uppercase;
}
