@import '../../variables.scss';

.calendar {
  width: 320px;
  box-sizing: border-box;

  padding: 18px;

  background-color: white;

  box-shadow: 0 10px 20px $fontColorLight5;
  border: 1px solid $fontColorLight25;
  border-radius: 4px;

  @media (max-width: 360px) {
    width: 100%;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 31px;
  }

  &__header-btn-prev,
  &__header-btn-next {
    display: flex;
    align-items: center;

    border: none;
    outline: none;

    background: white;

    cursor: pointer;
  }

  &__header &__header-btn-prev:focus,
  &__header &__header-btn-next:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__header-btn-prev svg {
    transform: rotateY(180deg);
  }

  &__header-info {
    color: $fontColor;

    font-family: $headerFont;
    font-size: 19px;
    line-height: 24px;
    font-weight: bold;
    text-transform: capitalize;
    text-align: center;
  }

  &__week-day-names,
  &__days {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    color: $fontColorLight50;

    font-size: 12px;
  }

  &__week-day-names {
    color: $mainColor;

    font-weight: bold;
    text-transform: capitalize;
  }

  &__days {
    margin-bottom: 20px;
  }

  &__week-day-name,
  &__day {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: calc(100% / 7);
    min-height: 40px;

    cursor: pointer;
  }

  &__day:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__day_select-one {
    border-radius: 20px;

    font-weight: bold;

    background: $secondColorGradient;
    color: white;
  }

  &__day_select-range-left,
  &__day_select-range-right {
    position: relative;

    border-radius: 20px;

    z-index: 1;

    font-weight: bold;

    background: $mainColorGradient;
    color: white;
  }

  &__day_select-range-left::before,
  &__day_select-range-right::before {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    content: '';
    width: 20px;
    height: 40px;

    z-index: -1;

    background: $mainColorGradient;
    opacity: 0.25;
  }

  &__day_select-range-right::before {
    left: 0;
  }

  &__day_select-range-middle {
    position: relative;
  }

  &__day_select-range-middle::before {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    width: 100%;
    height: 100%;

    z-index: 0;

    background: $mainColorGradient;
    opacity: 0.25;
  }

  &__day_disabled {
    color: $fontColorLight25;

    cursor: auto;
  }

  &__day_hidden {
    display: none;
  }

  &__actions {
    display: flex;
    justify-content: space-between;
  }

  &__action-clean,
  &__action-apply {
    display: block;
    background-color: white;
    outline: none;
    border: none;
    color: $mainColor;

    margin-right: 2px;
    padding: 0;

    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    font-weight: bold;

    cursor: pointer;
  }

  &__action-clean {
    color: $fontColorLight50;
  }

  &__action-clean:focus,
  &__action-apply:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }
}
