@import '../../../variables.scss';

.footer-copy-and-social {
  line-height: 18px;

  &__center {
    max-width: 1200px;

    margin: 0 auto;
    padding: 0 21px 0 20px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;

    padding-top: 26px;
    padding-bottom: 26px;

    @media (max-width: 992px) {
      justify-content: center;

      padding-top: 19px;
      padding-right: 20px;
    }
  }

  &__icons {
    display: flex;
    height: 16px;
  }

  &__icon {
    margin-left: 22px;
  }

  &__icon-link {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
  }

  &__icon-link:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__copyright {
    @media (max-width: 992px) {
      display: none;
    }
  }
}
