@import '../../variables.scss';

.text-field {
  display: block;
  position: relative;
  width: 100%;

  line-height: 14px;

  &__label {
    display: block;

    color: $fontColor;

    font-weight: bold;
    font-size: 12px;
    text-transform: uppercase;
    line-height: 14px;
  }

  &__input {
    display: block;
    width: 100%;

    padding: 11px 14px 13px;
    border-radius: 4px;
    border: 1px solid $fontColorLight25;
    outline: none;
    margin-top: 5px;

    color: $fontColorLight75;

    font-size: 14px;
    line-height: 14px;
  }

  &__input::-webkit-input-placeholder,
  &__input::-moz-placeholder,
  &__input::placeholder {
    color: $fontColorLight25;

    font-size: 14px;
    line-height: 18px;
  }

  &__input:focus,
  &__input:hover {
    border: 1px solid $fontColorLight75;

    color: $fontColorLight75;
  }

  &__submit-icon {
    position: absolute;
    right: 7px;
    bottom: 10px;

    border: none;
    outline: none;

    background: transparent;

    cursor: pointer;
  }

  &__submit-icon:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &_submit-icon-showed &__input {
    padding-right: 40px;
  }

  &__expand-more-icon {
    display: block;
    position: absolute;
    bottom: 14px;
    right: 15px;

    opacity: 0.5;
  }
}
