.date-range {
  display: block;
  position: relative;
  max-width: 100%;

  &__inputs {
    display: flex;
    justify-content: space-between;

    margin-bottom: 5px;
  }

  &__inputs .text-field:nth-child(2) {
    margin-left: 20px;
  }

  &__calendar {
    display: none;
    position: absolute;

    z-index: 2;
  }
}

.date-range_calendar-open .date-range__calendar {
  display: block;
}
