@import '../../../variables.scss';

.top-bar-user {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;

  @media (max-width: 600px) {
    display: block;
  }

  &__name {
    padding-left: 30px;

    color: $fontColorLight50;

    border-left: 1px solid $fontColorLight10;

    cursor: pointer;
  }

  &__actions {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding-bottom: 2px;

    @media (max-width: 600px) {
      padding-bottom: 10px;
    }

    @media (max-width: 400px) {
      display: block;
    }
  }

  &__action {
    @media (max-width: 600px) {
      display: block;

      margin-top: 5px;
    }

    @media (max-width: 400px) {
      margin-bottom: 5px;
    }
  }

  &__action:focus {
    box-shadow: 0 0 2px 1px $mainColor;
  }

  &__action:nth-child(1) {
    margin-right: 20px;
  }
}
