@import '../../variables.scss';

$buttonBorderRadius: 22px;

.button {
  padding: 10px 20px;
  border: none;
  outline: none;

  background: transparent;
  color: $mainColor;

  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  line-height: inherit;

  cursor: pointer;

  &__icon {
    display: none;
  }

  &:hover {
    color: $fontColorLight50;
  }

  &:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &_bordered {
    display: flex;
    align-items: center;
    position: relative;

    padding: 2px;
    border-radius: $buttonBorderRadius;
  }

  &_bordered > &__background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    border-radius: $buttonBorderRadius + 2;

    background: $mainColorGradient;
  }

  &_bordered > &__inner {
    display: block;
    z-index: 1;

    border: 2px solid rgba($color: #fff, $alpha: 0.5);
    padding: 6px 14px;
    border-radius: inherit;

    background: white;
  }

  &_bordered:hover > &__background {
    opacity: 0.5;
  }

  &_bordered:hover {
    color: $mainColor;
  }

  &_slim > &__inner {
    padding: 1px 16px 1px;
  }

  &_solid {
    border-radius: $buttonBorderRadius;

    background: $mainColorGradient;
    color: white;
  }

  .button_solid.button_slim &__inner {
    line-height: 12px;
  }

  &_solid:hover {
    opacity: 0.5;
    color: white;
  }

  &_iconed {
    display: flex;
    position: relative;
    align-items: center;
    width: 100%;

    border: none;
    padding: 15px 20px + 18px; // 18px - width icon
    border-radius: $buttonBorderRadius;

    background: $mainColorGradient;
    color: white;

    font-size: 12px;
    text-transform: uppercase;
    line-height: 14px;
  }

  &_iconed &__inner {
    width: 100%;
  }

  &_iconed &__icon {
    display: block;
    position: absolute;
    right: 12px;
    bottom: 10px;
  }

  &__icon > svg > path {
    fill: white;
  }

  &_iconed:hover {
    opacity: 0.5;
    color: white;
  }
}
