@import '../../variables.scss';

.footer {
  display: block;
  width: 100%;

  color: $fontColorLight75;

  font-size: 14px;
  line-height: 24px;

  &__center {
    max-width: 1200px;

    margin: 0 auto;
    padding: 0 21px 0 20px;

    @media (max-width: 1200px) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__navigation {
    padding-top: 100px;
    padding-bottom: 83px;
    border-bottom: 1px solid $fontColorLight10;

    @media (max-width: 992px) {
      border-bottom: none;
      padding-bottom: 0;
    }
  }

  &__navigation-inner {
    display: flex;
    justify-content: space-between;

    @media (max-width: 992px) {
      justify-content: center;

      text-align: center;
    }
  }

  &__navigation-description,
  &__navigation-subscription {
    width: 260px;
  }

  &__navigation-subscription {
    @media (max-width: 992px) {
      display: none;
    }
  }

  &__navigation-description {
    @media (max-width: 992px) {
      width: 100%;
      max-width: 560px;
    }
  }

  &__navigation-description-message {
    margin-top: 10px;
  }

  &__navigation-subscription-title {
    display: block;

    margin-top: 4px;
    margin-bottom: 18px;

    font-size: 12px;
    text-transform: uppercase;
  }

  &__navigation-subscription-message {
    display: block;

    margin-top: 13px;
    margin-bottom: 23px;
  }
}
